import React, { ReactNode } from 'react';
import { renderToString } from 'react-dom/server';
import type { MatrixConfigJSON } from '../shared/types/configJSON';

type FinderProps = {
  config: MatrixConfigJSON;
  initialQuery: any;
  initialResults: any;
};
export const render = (formData: React.ComponentType<FinderProps>, config: MatrixConfigJSON, initialQuery:any, initialResults:any, cb:any) => {
    const Component = formData,
        ServerRenderer = (
            <Component
                config={config}
                initialQuery={initialQuery}
                initialResults={initialResults}
            />
        ),
        formHtml = renderToString(ServerRenderer);

    cb(null, formHtml);
};
